import React, { useEffect, useState } from 'react'
import { Layout } from 'antd'
import classNames from 'classnames/bind'
import Sider from './Sider'
import Header from './Header'
import SettingDrawer from './SettingDrawer'
import { isObject } from '@/utils/util'
import scss from './index.module.scss'

const cx = classNames.bind(scss)

export type Theme = 'dark' | 'light'
export type MenuLocation = 'left' | 'top' | 'mix'
export type ContentWidthType = 'fluid' | 'fixed'

export interface Payload {
  collapsed?: boolean
  siderTheme?: Theme
  headerTheme?: Theme
  fixedHeader?: boolean
  menuLocation?: MenuLocation
  contentWidthType?: ContentWidthType
}

export interface SiderWidth {
  width: number,
  collapsedWidth: number
}

export interface LayoutConfig {
  collapsed: boolean
  siderTheme: Theme
  headerTheme: Theme
  fixedHeader: boolean
  menuLocation: MenuLocation
  contentWidthType: ContentWidthType
}

const BasicLayout: React.FC = ({ children }) => {
  const localLayoutConfig = getLocalLayoutConfig()
  const [drawerVisible, setDrawerVisible] = useState(false)
  const [layoutConfig, setLayoutConfig] = useState<LayoutConfig>({
    collapsed: false,
    siderTheme: 'dark',
    headerTheme: 'light',
    fixedHeader: true,
    menuLocation: 'left',
    contentWidthType: 'fluid',
    ...localLayoutConfig
  })
  const sideWidth: SiderWidth = {
    width: 210,
    collapsedWidth: 48
  }
  const { menuLocation, contentWidthType } = layoutConfig

  useEffect(() => {
    localStorage.setItem('layoutConfig', JSON.stringify(layoutConfig))
  }, [layoutConfig])

  function getLocalLayoutConfig() {
    let config = localStorage.getItem('layoutConfig')
    config = config && JSON.parse(config)
    return isObject(config) ? config : {}
  }

  function toggleDrawerVisible(arg: boolean) {
    setDrawerVisible(arg)
  }

  function assignLayoutConfig(payload: Payload): void {
    setLayoutConfig(state => ({
      ...state,
      ...payload
    }))
  }

  return (
    <Layout className={cx('layout')}>
      {
        (menuLocation === 'left' || menuLocation === 'mix') &&
        <Sider {...layoutConfig} sideWidth={sideWidth}/>
      }
      <Layout className={cx('layout__layout')}>
        <Header
          {...layoutConfig}
          sideWidth={sideWidth}
          assignLayoutConfig={assignLayoutConfig}
          toggleDrawerVisible={toggleDrawerVisible}/>
        <Layout.Content className={cx('layout__content', contentWidthType)}>
          {children}
        </Layout.Content>
      </Layout>
      <SettingDrawer
        {...layoutConfig}
        drawerVisible={drawerVisible}
        assignLayoutConfig={assignLayoutConfig}
        toggleDrawerVisible={toggleDrawerVisible}/>
    </Layout>
  )
}

export default BasicLayout
