import React from 'react'
import { Layout } from 'antd'
import classNames from 'classnames/bind'
import Menus from '@/components/Layout/Menus'
import SiteLogo from '@/components/Layout/SiteLogo'
import { LayoutConfig, SiderWidth } from '@/components/Layout/index'
import scss from './index.module.scss'

interface SiderProps extends LayoutConfig {
  sideWidth: SiderWidth
}

const cx = classNames.bind(scss)

const Sider: React.FC<SiderProps> = (props) => {
  const { sideWidth, siderTheme, collapsed, menuLocation } = props
  return (
    <Layout.Sider
      collapsible
      trigger={null}
      theme={siderTheme}
      collapsed={collapsed}
      width={sideWidth.width}
      collapsedWidth={sideWidth.collapsedWidth}
      className={cx('sider')}
    >
      <SiteLogo {...props} theme={siderTheme}/>
      <Menus
        mode={'inline'}
        collapsed={collapsed}
        theme={siderTheme}
        className={cx({ 'no-padding': menuLocation === 'mix' })}/>
    </Layout.Sider>
  )
}
export default Sider
