import React from 'react'
import {
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  SettingOutlined
} from '@ant-design/icons'
import { Layout } from 'antd'
import classNames from 'classnames/bind'
import Menus from '@/components/Layout/Menus'
import SiteLogo from '@/components/Layout/SiteLogo'
import LangDropdown from '@/components/LangDropdown'
import UserDropdown from './UserDropdown'
import ComponentSizeDropdown from './ComponentSizeDropdown'
import { LayoutConfig, Payload, SiderWidth } from '@/components/Layout/index'
import scss from '../index.module.scss'

interface HeaderProps extends LayoutConfig {
  sideWidth: SiderWidth
  toggleDrawerVisible: (arg: boolean) => void
  assignLayoutConfig: (payload: Payload) => void
}

const cx = classNames.bind(scss)

const Index: React.FC<HeaderProps> = (props) => {
  const {
    collapsed,
    headerTheme,
    menuLocation,
    assignLayoutConfig,
    toggleDrawerVisible
  } = props
  const headerInnerClassName = cx('header__inner', 'fixed', {
    'z-index-3': menuLocation === 'mix'
  })
  return (
    <Layout.Header className={cx('header', headerTheme)}>
      <div className={headerInnerClassName}>
        <SiteLogo {...props} theme={headerTheme}/>
        {
          menuLocation !== 'top' &&
          <span className={cx('header__action')}
            onClick={() => assignLayoutConfig({ collapsed: !collapsed })}>
            {collapsed ? <MenuUnfoldOutlined/> : <MenuFoldOutlined/>}
          </span>
        }
        <div className={cx('header__center')}>
          {
            menuLocation === 'top' &&
            <Menus mode={'horizontal'} collapsed={true} theme={headerTheme}/>
          }
        </div>
        <div className={cx('header__right')}>
          <ComponentSizeDropdown className={cx('header__action')}/>
          <LangDropdown className={cx('header__action')}/>
          <span onClick={() => toggleDrawerVisible(true)} className={cx('header__action')}>
            <SettingOutlined/>
          </span>
          <UserDropdown/>
        </div>
      </div>
    </Layout.Header>
  )
}

export default Index
