import React, { useEffect, useState } from 'react'
import { useHistory, useLocation } from 'react-router'
import { useSelector } from 'react-redux'
import { MenuItem, State } from '@/store/types'
import antIcons from '@/utils/icons'
import { Menu } from 'antd'
import { ClickParam } from 'antd/es/menu'
import classNames from 'classnames/bind'
import { Theme } from '@/components/Layout/index'
import scss from './index.module.scss'

const { SubMenu } = Menu
const cx = classNames.bind(scss)

interface MenusProps {
  mode: 'vertical' | 'horizontal' | 'inline'
  theme: Theme
  collapsed: boolean
  className?: string
}

const Menus: React.FC<MenusProps> = (props) => {
  const { mode, theme, collapsed, className } = props
  const location = useLocation()
  const history = useHistory()
  const [openKeys, setOpenKeys] = useState<string[]>([])
  const menus = useSelector<State, MenuItem[]>(state => state.userInfo.menus || [])

  useEffect(getDefaultOpenKeys, [menus, location])

  function getDefaultOpenKeys() {
    const key = getPathParent(menus)
    if (key) setOpenKeys([...openKeys, key])
  }

  function renderMenuItem(menu: MenuItem): React.ReactNode {
    const AntIcon = menu.icon && antIcons[menu.icon]
    return (
      <Menu.Item key={menu.path || menu.id}>
        {menu.icon && <AntIcon/>}
        <span>{menu.name}</span>
      </Menu.Item>
    )
  }

  function renderSubMenu(menu: MenuItem): React.ReactNode {
    const AntIcon = menu.icon && antIcons[menu.icon]
    return (
      <SubMenu
        key={menu.id}
        title={menu.name}
        icon={menu.icon && <AntIcon/>}
        onTitleClick={collapsed ? undefined : onTitleClick}
      >
        {
          menu.children.map(menu => (
            menu.children.length ? renderSubMenu(menu) : renderMenuItem(menu)
          ))
        }
      </SubMenu>
    )
  }

  function getPathParent(menus: MenuItem[]): any {
    let parent
    for (let i = menus.length; i--;) {
      if (menus[i].children.length) {
        parent = getPathParent(menus[i].children)
      } else if (menus[i].path === location.pathname) {
        parent = menus[i].parent
      }
      if (parent) return parent
    }
  }

  function onTitleClick({ key }: any) {
    let keys = [...openKeys]
    const isExist = keys.some(item => item === key)
    if (isExist) {
      keys = keys.filter(item => item !== key)
    } else {
      keys.push(key)
    }
    setOpenKeys(keys)
  }

  function clickMenu({ key, keyPath }: ClickParam) {
    setOpenKeys(keyPath.slice(1))
    key.startsWith('/') && history.push(key)
  }

  return collapsed
    ? <Menu
      theme={theme}
      mode={mode}
      inlineIndent={16}
      onClick={clickMenu}
      selectedKeys={[location.pathname]}
      className={cx('menu', className)}
    >
      {
        menus.map((menu) => (
          menu.children.length ? renderSubMenu(menu) : renderMenuItem(menu)
        ))
      }
    </Menu>
    : <Menu
      theme={theme}
      mode={mode}
      inlineIndent={16}
      onClick={clickMenu}
      openKeys={openKeys}
      selectedKeys={[location.pathname]}
      className={cx('menu', className)}
    >
      {
        menus.map((menu) => (
          menu.children.length ? renderSubMenu(menu) : renderMenuItem(menu)
        ))
      }
    </Menu>
}

export default Menus
