import React from 'react'
import { Dropdown, Menu } from 'antd'
import { setLocale } from '@/store/actions/common'
import { Locale, State } from '@/store/types'
import { useDispatch, useSelector } from 'react-redux'
import { ClickParam } from 'antd/es/menu'
import { LanguageIcon } from '@/components/icons/CustomIcons'

interface Props {
  className?: string;
}

interface Language {
  key: Locale
  icon: string
  label: string
}

const LangDropdown: React.FC<Props> = ({ className }) => {
  const dispatch = useDispatch()
  const languages: Language[] = [
    {
      key: 'zh-CN',
      label: 'ÁÆÄ‰Ωì‰∏≠Êñá',
      icon: 'üá®üá≥'
    },
    {
      key: 'zh-TW',
      label: 'ÁπÅ‰Ωì‰∏≠Êñá',
      icon: 'üá≠üá∞'
    },
    {
      key: 'en-US',
      label: 'English',
      icon: 'üá∫üá∏'
    }
  ]
  const locale = useSelector<State, Locale>(state => state.locale)
  const changeLang = ({ key }: ClickParam): any => dispatch(setLocale(key as Locale))
  const langMenu = (
    <Menu onClick={changeLang} style={{ minWidth: 160 }} selectedKeys={[locale]}>
      {languages.map((item) => (
        <Menu.Item key={item.key}>
          <span role="img" aria-label={item.label}>
            {item.icon}
          </span>
          <span>
            &nbsp;{item.label}
          </span>
        </Menu.Item>
      ))}
    </Menu>
  )
  return (
    <Dropdown overlay={langMenu}>
      <span className={className}>
        <LanguageIcon/>
      </span>
    </Dropdown>
  )
}

export default LangDropdown
