import React from 'react'
import {
  UserOutlined,
  LogoutOutlined,
  SettingOutlined
} from '@ant-design/icons'
import classNames from 'classnames/bind'
import { useHistory } from 'react-router'
import { useDispatch, useSelector } from 'react-redux'
import { State } from '@/store/types'
import { ClickParam } from 'antd/es/menu'
import { ResponseData } from '@/api/types'
import { signOut } from '@/store/actions/user'
import { Avatar, Dropdown, Menu, message } from 'antd'
import { useIntl } from 'react-intl'
import scss from '../index.module.scss'

const cx = classNames.bind(scss)

const UserDropdown: React.FC = () => {
  const intl = useIntl()
  const history = useHistory()
  const dispatch = useDispatch()
  const username = useSelector<State, string | undefined>(state => state.userInfo.username)

  function logout({ key }: ClickParam) {
    if (key === 'logout') {
      dispatch<Promise<ResponseData>>(signOut()).then(({ success }) => {
        if (success) {
          message.success('ÈÄÄÂá∫ÊàêÂäü')
          history.push('/user/login')
        }
      })
    }
  }

  const menu = (
    <Menu onClick={logout} style={{ minWidth: 160 }}>
      <Menu.Item key="center">
        <UserOutlined/>
        <span>
          {intl.formatMessage({ id: 'layout.userDropdown.center' })}
        </span>
      </Menu.Item>
      <Menu.Item key="setting">
        <SettingOutlined/>
        <span>
          {intl.formatMessage({ id: 'layout.userDropdown.setting' })}
        </span>
      </Menu.Item>
      <Menu.Divider/>
      <Menu.Item key="logout">
        <LogoutOutlined/>
        <span>
          {intl.formatMessage({ id: 'layout.userDropdown.logout' })}
        </span>
      </Menu.Item>
    </Menu>
  )

  return (
    <Dropdown overlay={menu}>
      <span className={cx('header__action')}>
        <Avatar icon={<UserOutlined/>} className={cx('header__avatar')}/>
        <span className={cx('header__username')}>{username}</span>
      </span>
    </Dropdown>
  )
}

export default UserDropdown
