import React from 'react'
import { Dropdown, Menu } from 'antd'
import { ClickParam } from 'antd/es/menu'
import { ComponentSize, State } from '@/store/types'
import { useDispatch, useSelector } from 'react-redux'
import { SizeIcon } from '@/components/icons/CustomIcons'
import { setComponentSize } from '@/store/actions/common'
import { useIntl } from 'react-intl'

interface Props {
  className?: string;
}

interface SizeItem {
  key: ComponentSize
  label: string
}

const ComponentSizeDropdown: React.FC<Props> = ({ className }) => {
  const intl = useIntl()
  const dispatch = useDispatch()
  const sizes: SizeItem[] = [
    {
      key: 'small',
      label: intl.formatMessage({ id: 'layout.componentSizeDropdown.small' })
    },
    {
      key: 'middle',
      label: intl.formatMessage({ id: 'layout.componentSizeDropdown.middle' })
    },
    {
      key: 'large',
      label: intl.formatMessage({ id: 'layout.componentSizeDropdown.large' })
    }
  ]
  const componentSize = useSelector<State, ComponentSize>(state => state.componentSize)
  const changeLang = ({ key }: ClickParam): any => dispatch(setComponentSize(key as ComponentSize))
  const sizeMenu = (
    <Menu onClick={changeLang} style={{ minWidth: 160 }} selectedKeys={[componentSize]}>
      {sizes.map((item) => (
        <Menu.Item key={item.key}>
          <span>
            {item.label}
          </span>
        </Menu.Item>
      ))}
    </Menu>
  )
  return (
    <Dropdown overlay={sizeMenu} placement="bottomLeft">
      <span className={className}>
        <SizeIcon/>
      </span>
    </Dropdown>
  )
}

export default ComponentSizeDropdown
